.mobile_menu {
    width: 100%; /* Changed from 100vw to 100% */
    height: 100vh;
    display: none;
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    z-index: 999 !important;
    opacity: 0;
    left: 0;
    transition: all 0.3s ease;
    box-shadow: 0px 15px 80px rgba(0, 0, 0, 0.3);
    transform: translateX(-100%);
    overflow-x: hidden; /* Ensure no horizontal overflow */
}

.mobile_nav {
    width: 40vw;
    height: 100vh;
    background-color: #6852ba;
    padding: 2rem;
}

.mobile_menu.active {
    opacity: 1;
    transform: translateX(0);
}

.mobile_menu h2 {
    margin-bottom: 2rem;
}

.mobile_menu ul {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    list-style: none;
    margin-left: -2rem;
    color: #fff;
}

.mobile_menu a {
    list-style: none;
    text-decoration: none;
    font-size: large;
    font-weight: 600;
    color: #fff; /* Ensure links are visible */
}

.mobile_menu a:hover {}

.hire {
    /* margin-left: 0.5rem; */
    font-size: 0.9rem;
    font-weight: 500;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 0.4rem;
    color: #fff;
    padding: 0.9rem 1rem;
    outline: 1.5px solid transparent;
    line-height: 0;
    cursor: pointer;
    background: linear-gradient(90deg, #a993fe 0%, #7e61e7 99.87%);
    transition: all 0.5s ease;
}

.hire:hover {
    border: 1.1px solid #a993fe;
}

@media (max-width: 769px) {
    .mobile_menu {
        display: block;
    }
    .hire{
        margin-left: 0.5rem;
    }

    .mobile_menu a {
        color: #fff;
    }
}


@media (max-width:355px){
    .hire{
        padding: .9rem .8rem;
    }
}